import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { getCurrentUserProfile, getLocalMajorData, getMajorData } from '../../services/tournamentService'; // Importar getLocalMajorData o getMajorData
import { TwitchUserProfile } from '../../types/fantasyTypes';
import GridPattern from '../GridPattern'; // Import GridPattern
import { MajorData, Stage } from '../../types/hltvTypes'; // Importar MajorData y Stage

// Iconos SVG en lnea
const ShieldCheckIconSVG = ({ className = "w-10 h-10" }) => (
  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={className}>
    <path strokeLinecap="round" strokeLinejoin="round" d="M9 12.75L11.25 15 15 9.75m-3-7.036A11.959 11.959 0 013.598 6 11.99 11.99 0 003 9.749c0 5.592 3.824 10.29 9 11.623 5.176-1.332 9-6.03 9-11.622 0-1.31-.21-2.571-.598-3.751h-.152c-3.196 0-6.1-1.248-8.25-3.285z" />
  </svg>
);

const ListBulletIconSVG = ({ className = "w-10 h-10" }) => (
  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={className}>
    <path strokeLinecap="round" strokeLinejoin="round" d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5" />
  </svg>
);

const UserCircleIconSVG = ({ className = "w-10 h-10" }) => (
  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={className}>
    <path strokeLinecap="round" strokeLinejoin="round" d="M17.982 18.725A7.488 7.488 0 0012 15.75a7.488 7.488 0 00-5.982 2.975m11.963 0a9 9 0 10-11.963 0m11.963 0A8.966 8.966 0 0112 21a8.966 8.966 0 01-5.982-2.275M15 9.75a3 3 0 11-6 0 3 3 0 016 0z" />
  </svg>
);

const ArrowRightCircleIconSVG = ({ className = "w-10 h-10" }) => (
  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={className}>
    <path strokeLinecap="round" strokeLinejoin="round" d="M12.75 15l3-3m0 0l-3-3m3 3h-7.5M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
  </svg>
);

const BookOpenIconSVG = ({ className = "w-10 h-10" }) => (
  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className={className}>
    <path strokeLinecap="round" strokeLinejoin="round" d="M12 6.042A8.967 8.967 0 006 3.75c-1.052 0-2.062.18-3 .512v14.25A8.987 8.987 0 016 18c2.305 0 4.408.867 6 2.292m0-14.25a8.966 8.966 0 016-2.292c1.052 0 2.062.18 3 .512v14.25A8.987 8.987 0 0018 18a8.967 8.967 0 00-6 2.292m0-14.25v14.25" />
  </svg>
);

const FantasyDashboard: React.FC = () => {
  const [currentUser, setCurrentUser] = useState<TwitchUserProfile | null>(null);
  const [majorData, setMajorData] = useState<MajorData | null>(null);
  const [activeSwissStageId, setActiveSwissStageId] = useState<number | null>(null);
  const [activeTournamentIdForPlayoffs, setActiveTournamentIdForPlayoffs] = useState<number | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const user = await getCurrentUserProfile();
        setCurrentUser(user);

        // Intenta obtener datos locales primero para evitar refetch innecesario
        let tournamentData = getLocalMajorData(); 
        if (!tournamentData) {
          // Si no hay datos locales, o si queremos forzar refresh para el torneo live:
          // Necesitamos el slug del torneo "live" o el ms reciente si no hay live.
          // Esta lgica podra estar en un helper o ser ms robusta.
          // Por ahora, asumimos que getMajorData sin slug trae el live/ms reciente.
          tournamentData = await getMajorData(false, null); 
        }
        setMajorData(tournamentData);

        if (tournamentData && tournamentData.stages) {
          // Encontrar la primera fase suiza (SWISS) abierta
          const swissStages = Object.values(tournamentData.stages).filter(
            (stage): stage is Stage & { id: number; type: string; fantasyStatus: string } => 
              typeof stage.id === 'number' && stage.type === 'SWISS'
          );
          
          const openSwissStage = swissStages.sort((a,b) => (a.order || 0) - (b.order || 0)).find(
            stage => stage.fantasyStatus === 'OPEN'
          );
          setActiveSwissStageId(openSwissStage ? openSwissStage.id : null);
          
          // Asumir que el torneo de estos datos es el relevante para los playoffs
          // El ID del torneo no est directamente en MajorData, necesitaramos obtenerlo
          // de una llamada a /api/tournaments/ y encontrar el que es is_live=true
          // o pasarlo de alguna forma. Por ahora, si hay una fase de playoffs, usamos su tournament ID
          // Esto es una simplificacin; idealmente el backend dara esta info ms directamente para el fantasy.
          // Como placeholder, si `tournamentData.id` existiera lo usaramos.
          // Alternativa: buscar el torneo "live" desde una lista de torneos.
          // Por ahora, dejaremos el enlace de playoffs un poco ms genrico o usaremos un ID fijo si no podemos derivarlo fcilmente.
          // Si hay datos del torneo, y hay alguna fase, asumimos que esa fase pertenece al torneo actual.
          // El modelo de Stage tiene un `tournament` ForeignKey. Si serializramos `tournament.id` en cada Stage, podramos usarlo.
          
          // Solucin temporal: si hay datos, intentaremos usar el ID de la primera fase (si es un ID de torneo)
          // Esto NO es robusto. Idealmente, getMajorData() debera devolver un tournament_id
          const firstStageKey = Object.keys(tournamentData.stages)[0];
          if (firstStageKey) {
            //  Como el `tournamentId` no est en `MajorData`, lo dejaremos como estaba (placeholder) o
            //  el usuario deber navegar desde una lista de torneos para los picks de playoffs.
            //  Para la tarjeta en el dashboard, vamos a asumir que si hay un torneo activo (majorData), ese es el relevante.
            //  La API de playoff picks espera un tournamentId.
            //  Si tenemos `majorData.id` (ID del torneo), lo usaramos. Como no, usaremos un placeholder.
             // OJO: Esto depende de que `tournamentId` se pueda inferir o se pase.
             // Por ahora, vamos a buscar un torneo live de /api/tournaments/
             // Esta llamada no est en tournamentService, habra que aadirla o modificar getMajorData
             // Simplificacin: si `majorData` existe, usaremos un ID de torneo fijo o el primero que encontremos
             // setActiveTournamentIdForPlayoffs(tournamentData.id_del_torneo_si_existiera);
          }


        }
      } catch (err) {
        console.error("Error fetching data for dashboard:", err);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  const cardLinkClassName = "block p-6 bg-neutral-800 hover:bg-neutral-700 rounded-xl shadow-lg transition-all duration-300 ease-in-out transform hover:scale-105";
  const cardTitleClassName = "text-xl font-semibold text-white mb-2";
  const cardDescriptionClassName = "text-neutral-400 text-sm";
  const iconClassName = "w-10 h-10 text-primary-400 mb-3";

  // Placeholder para ID de torneo para playoffs, ya que no lo tenemos directamente de majorData
  const placeholderTournamentIdForPlayoffs = majorData ? 1 : null; // Asumir 1 si hay datos, o null

  if (loading) {
    return (
      <div className="min-h-[calc(100vh-4rem)] relative isolate bg-black flex items-center justify-center text-white">
        <GridPattern />
        <p className="relative z-10">Cargando Dashboard...</p>
      </div>
    );
  }

  return (
    <div className="min-h-[calc(100vh-4rem)] relative isolate bg-black text-white"> 
      <GridPattern />
      <div className="relative z-10 max-w-5xl mx-auto p-4 sm:p-8 pt-8">
        <header className="mb-10 text-center">
          <h1 className="text-4xl sm:text-5xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-primary-400 to-secondary-500 mb-3">
            Fantasy League Hub
          </h1>
          <p className="text-neutral-400 text-lg">
            Demuestra tu conocimiento de CS2 y compite por el primer puesto en el Major!
          </p>
        </header>

        {currentUser && (
          <section className="mb-12 p-6 bg-neutral-800 rounded-xl shadow-lg text-center">
            <h2 className="text-2xl font-semibold text-white mb-2">
              Bienvenido de nuevo, <span className="text-primary-400">{currentUser.twitch_username || currentUser.user.username}</span>!
            </h2>
            <p className="text-neutral-300">
              Puntos Totales en Fantasy: <span className="font-bold text-xl text-secondary-400">{currentUser.total_fantasy_points}</span>
            </p>
          </section>
        )}

        {/* Seccin de Introduccin y Reglas */}
        <section className="mb-12 p-6 bg-neutral-800 rounded-xl shadow-lg">
          <div className="flex items-center mb-4">
            <BookOpenIconSVG className="w-8 h-8 text-primary-400 mr-3" />
            <h2 className="text-2xl font-semibold text-white">Cmo Jugar al Fantasy del Major?</h2>
          </div>
          <h3 className="text-xl font-semibold text-primary-300 mb-2">Reglas Generales y Puntuacin:</h3>
          <ul className="list-disc list-inside text-neutral-300 space-y-2">
            <li>
              <strong>Fase de Grupos (Suiza):</strong>
              <ul className="list-circle list-inside ml-4 mt-1 text-neutral-400 text-sm space-y-1">
                <li>Predice los equipos que terminarn con rcord de 3-0.</li>
                <li>Predice los equipos que terminarn con rcord de 0-3.</li>
                <li>Predice el resto de equipos que avanzarn a la siguiente fase.</li>
                <li>Cada prediccin correcta otorga puntos. Los equipos "underdog" (bajo seed) pueden dar puntos extra!</li>
              </ul>
            </li>
            <li>
              <strong>Fase de Playoffs (Eliminatoria):</strong>
              <ul className="list-circle list-inside ml-4 mt-1 text-neutral-400 text-sm space-y-1">
                <li>Predice los 4 equipos que ganarn sus partidos de Cuartos de Final.</li>
                <li>Predice los 2 equipos que ganarn sus partidos de Semifinales.</li>
                <li>Predice al gran Campen del Major!</li>
                <li>Los aciertos en rondas ms avanzadas suelen otorgar ms puntos.</li>
              </ul>
            </li>
            <li>Las selecciones para cada fase se bloquean una vez que la fase comienza o segn lo indique el administrador.</li>
            <li>Los puntos se calculan y actualizan al finalizar cada fase o conjunto de partidos relevantes.</li>
          </ul>
        </section>

        <h2 className="text-3xl font-semibold text-center text-white mb-8">Acciones de Fantasy</h2>
        <section className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {/* Card para Picks de Fase */}
          {activeSwissStageId ? (
            <Link to={`/fantasy/stage/${activeSwissStageId}/picks`} className={cardLinkClassName}>
              <ArrowRightCircleIconSVG className={iconClassName} />
              <h3 className={cardTitleClassName}>Picks Fase de Grupos</h3>
              <p className={cardDescriptionClassName}>
                {majorData?.stages?.[`phase${activeSwissStageId}`]?.name || 'Fase actual abierta'}. Elige tus predicciones para 3-0, 0-3 y los que avanzan.
              </p>
            </Link>
          ) : (
            <div className={`${cardLinkClassName} opacity-70 cursor-not-allowed`}>
              <ArrowRightCircleIconSVG className={`${iconClassName} opacity-50`} />
              <h3 className={cardTitleClassName}>Picks Fase de Grupos</h3>
              <p className={cardDescriptionClassName}>
                Actualmente no hay una fase de grupos abierta para realizar picks.
              </p>
            </div>
          )}

          {/* Card para Picks de Playoffs */}
          {placeholderTournamentIdForPlayoffs ? ( // Usar el placeholder o un ID de torneo real si se obtiene
            <Link to={`/fantasy/tournament/${placeholderTournamentIdForPlayoffs}/playoffs/picks`} className={cardLinkClassName}>
              <ShieldCheckIconSVG className={iconClassName} />
              <h3 className={cardTitleClassName}>Picks de Playoffs</h3>
              <p className={cardDescriptionClassName}>
                Predice los ganadores de Cuartos, Semifinales y el Campen del torneo {majorData?.name ? `(${majorData.name})` : ''}.
              </p>
            </Link>
          ) : (
             <div className={`${cardLinkClassName} opacity-70 cursor-not-allowed`}>
              <ShieldCheckIconSVG className={`${iconClassName} opacity-50`} />
              <h3 className={cardTitleClassName}>Picks de Playoffs</h3>
              <p className={cardDescriptionClassName}>
                Informacin del torneo para playoffs no disponible actualmente.
              </p>
            </div>
          )}
          
          {/* Card para Leaderboard */}
          <Link to="/fantasy/leaderboard" className={cardLinkClassName}>
            <ListBulletIconSVG className={iconClassName} />
            <h3 className={cardTitleClassName}>Leaderboard</h3>
            <p className={cardDescriptionClassName}>
              Consulta la clasificacin general y mira quin lidera la competencia.
            </p>
          </Link>

          {/* Card para Mi Perfil Fantasy (si el usuario est logueado) */}
          {currentUser && (
            <Link to={`/fantasy/profile/${currentUser.user.username}`} className={`${cardLinkClassName} md:col-span-2 lg:col-span-1`}>
              <UserCircleIconSVG className={iconClassName} />
              <h3 className={cardTitleClassName}>Mi Perfil Fantasy</h3>
              <p className={cardDescriptionClassName}>
                Revisa tu historial de picks, puntos obtenidos por fase y tu progreso general.
              </p>
            </Link>
          )}
           {!currentUser && (
             <div 
                onClick={() => { /* Podramos llamar a twitchLogin() aqu si se quiere un login directo */ }} 
                className={`${cardLinkClassName} md:col-span-2 lg:col-span-1 ${!currentUser ? 'cursor-pointer hover:border-primary-500 border-2 border-transparent' : ''}`}
             >
              <UserCircleIconSVG className={iconClassName} />
              <h3 className={cardTitleClassName}>Mi Perfil Fantasy</h3>
              <p className={cardDescriptionClassName}>
                Inicia sesin para ver tu perfil y tus picks. (El login est en la Navbar)
              </p>
            </div>
          )}
        </section>
        
      </div>
    </div>
  );
};

export default FantasyDashboard; 