import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import GridPattern from '../GridPattern';
import { getStageFantasyInfo, submitFantasyPhasePicks } from '../../services/tournamentService';
import { StageFantasyInfo, FantasyPhasePickPayload, StageTeamInfoForFantasy } from '../../types/fantasyTypes';
import {
  DndContext,
  closestCenter,
  KeyboardSensor,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
  DragStartEvent,
  DragOverlay,
  useDraggable,
  useDroppable,
  UniqueIdentifier
} from '@dnd-kit/core';
import { CSS } from '@dnd-kit/utilities';

interface TeamCardProps {
  team: StageTeamInfoForFantasy;
  isSelected?: boolean;
  isDragging?: boolean;
  isDragOverlay?: boolean;
  isUnderdogBonus?: boolean;
}

const TeamCard: React.FC<TeamCardProps> = ({ team, isSelected, isDragging, isDragOverlay, isUnderdogBonus }) => {
  const style = {
    opacity: isDragging ? 0.5 : 1,
    transform: isDragOverlay ? 'scale(1.05)' : 'scale(1)',
    boxShadow: isDragOverlay ? '0 0 15px rgba(0,0,0,0.5)' : (isUnderdogBonus ? '0 0 12px 2px rgba(250, 176, 5, 0.7)': undefined),
    cursor: isDragOverlay ? 'grabbing' : 'grab',
  };

  const logoSrc = team.logo && (team.logo.startsWith('http') || team.logo.startsWith('/')) 
                  ? team.logo 
                  : `/team-logos/${team.name.toLowerCase().replace(/\s+/g, '')}.png`;

  return (
    <div
      style={style}
      className={`p-2 rounded-lg shadow-md transition-all flex flex-col items-center justify-center relative
                  bg-neutral-800
                  ${isUnderdogBonus ? 'border-yellow-400' : 'border-neutral-700'}
                  ${isDragOverlay ? 'z-50' : ''}
                  border w-32 h-36`}
    >
      {isUnderdogBonus && (
        <div className="absolute top-1 right-1 bg-yellow-500 text-black text-xs font-bold px-1.5 py-0.5 rounded-sm shadow-md">
          x1.5 Pts
        </div>
      )}
      <img
        src={logoSrc}
        alt={`${team.name} logo`}
        className="w-24 h-24 object-contain mb-2 rounded-sm"
        onError={(e) => { (e.target as HTMLImageElement).src = '/team-logos/default.png'; }}
      />
      <p className="text-sm text-neutral-300 mt-1">Seed: {team.seed}</p>
    </div>
  );
};

interface DraggableTeamCardProps {
  id: UniqueIdentifier;
  team: StageTeamInfoForFantasy;
  isSelected?: boolean;
  onCardClick?: () => void;
  isUnderdogBonus?: boolean;
}

const DraggableTeamCard: React.FC<DraggableTeamCardProps> = ({ id, team, isSelected, onCardClick, isUnderdogBonus }) => {
  const { attributes, listeners, setNodeRef, transform, isDragging } = useDraggable({
    id: id,
    data: { team: team, type: 'team' },
  });

  const style = {
    transform: CSS.Translate.toString(transform),
    opacity: isDragging ? 0.5 : 1,
    zIndex: isDragging ? 100 : 'auto',
    cursor: onCardClick ? 'pointer' : (isDragging ? 'grabbing' : 'grab'),
  };
  
  return (
    <div 
      ref={setNodeRef} 
      style={style} 
      {...listeners}
      {...attributes}
      onClick={(e) => {
        if (onCardClick) {
          e.stopPropagation();
          onCardClick();
        }
      }}
    >
      <TeamCard team={team} isSelected={isSelected} isDragging={isDragging} isUnderdogBonus={isUnderdogBonus} />
    </div>
  );
};

interface DroppableSlotProps {
  id: UniqueIdentifier;
  team: StageTeamInfoForFantasy | null; 
  fantasyStatus: StageFantasyInfo['fantasy_status'];
  categoryTitle: string;
  onRemoveClick?: () => void;
  isUnderdogBonus?: boolean;
}

const DroppableSlot: React.FC<DroppableSlotProps> = ({ id, team, fantasyStatus, categoryTitle, onRemoveClick, isUnderdogBonus }) => {
  const { isOver, setNodeRef } = useDroppable({
    id: id,
    data: { type: 'slot', slotId: id, category: categoryTitle }, 
  });

  const canDrop = fantasyStatus === 'OPEN';
  const baseClasses = "w-36 h-44 rounded-lg shadow-md flex flex-col justify-center items-center p-1"; 
  const borderStyle = isOver && canDrop ? "border-primary-500 ring-2 ring-primary-400" : "border-neutral-600";
  const bgStyle = canDrop ? "bg-neutral-800/70" : "bg-neutral-700/50";
  const finalDropzoneClass = `${baseClasses} ${bgStyle} border-2 border-dashed ${borderStyle} transition-all`;

  return (
    <div ref={canDrop ? setNodeRef : null} className={finalDropzoneClass}>
      {team ? (
        <DraggableTeamCard 
          id={`picked-${id}-${team.id}`}
          team={team} 
          isSelected={true} 
          onCardClick={fantasyStatus === 'OPEN' && onRemoveClick ? onRemoveClick : undefined}
          isUnderdogBonus={isUnderdogBonus}
        />
      ) : (
        canDrop ? (
          <p className="text-neutral-500 text-sm text-center">Arrastrar aqu</p> 
        ) : (
          <p className="text-neutral-600 text-sm text-center">Bloqueado</p> 
        )
      )}
    </div>
  );
};

const getTeamIdAtSlot = (
  category: string, 
  index: number, 
  p30: (number | null)[], 
  pA: (number | null)[], 
  p03: (number | null)[]
): number | null => {
  if (category === 'zone-30') return p30[index] !== undefined ? p30[index] : null;
  if (category === 'zone-advance') return pA[index] !== undefined ? pA[index] : null;
  if (category === 'zone-03') return p03[index] !== undefined ? p03[index] : null;
  return null;
};

const placeTeamInSlot = (
  teamId: number | null, 
  category: string, 
  index: number, 
  p30: (number | null)[], 
  pA: (number | null)[], 
  p03: (number | null)[]
) => {
  if (category === 'zone-30' && index < p30.length) p30[index] = teamId;
  else if (category === 'zone-advance' && index < pA.length) pA[index] = teamId;
  else if (category === 'zone-03' && index < p03.length) p03[index] = teamId;
};

const FantasyStagePicks: React.FC = () => {
  const { stageId } = useParams<{ stageId: string }>();
  const [stageInfo, setStageInfo] = useState<StageFantasyInfo | null>(null);
  
  const [pickedTeams30, setPickedTeams30] = useState<(number | null)[]>([]);
  const [pickedTeamsAdvance, setPickedTeamsAdvance] = useState<(number | null)[]>([]);
  const [pickedTeams03, setPickedTeams03] = useState<(number | null)[]>([]);
  
  const [availableTeams, setAvailableTeams] = useState<StageTeamInfoForFantasy[]>([]);
  const [underdogBonusTeamIds, setUnderdogBonusTeamIds] = useState<number[]>([]);
  
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [submitMessage, setSubmitMessage] = useState<string | null>(null);
  const [activeDragItem, setActiveDragItem] = useState<StageTeamInfoForFantasy | null>(null);

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor)
  );

  const { setNodeRef: setAvailableZoneNodeRef } = useDroppable({ id: 'zone-available', data: {type: 'zone', category: 'available'} });

  useEffect(() => {
    if (stageId) {
      setIsLoading(true);
      getStageFantasyInfo(parseInt(stageId))
        .then(data => {
          setStageInfo(data);
          if (data.underdog_bonus_team_ids) {
            setUnderdogBonusTeamIds(data.underdog_bonus_team_ids);
          }
          let initialAvailable = [...data.teams];
          const pickedIdsInSlots = new Set<number>();

          const num30 = data.rules?.num_teams_3_0 || 0;
          const numAdvance = data.rules?.num_teams_advance || 0;
          const num03 = data.rules?.num_teams_0_3 || 0;

          let initialPicked30 = new Array(num30).fill(null);
          let initialPickedAdvance = new Array(numAdvance).fill(null);
          let initialPicked03 = new Array(num03).fill(null);

          if (data.user_pick) {
            data.user_pick.teams_3_0_details.forEach((team, index) => {
              if (index < num30) {
                initialPicked30[index] = team.id;
                pickedIdsInSlots.add(team.id);
              }
            });
            data.user_pick.teams_advance_details.forEach((team, index) => {
              if (index < numAdvance) {
                initialPickedAdvance[index] = team.id;
                pickedIdsInSlots.add(team.id);
              }
            });
            data.user_pick.teams_0_3_details.forEach((team, index) => {
              if (index < num03) {
                initialPicked03[index] = team.id;
                pickedIdsInSlots.add(team.id);
              }
            });
          }
          setPickedTeams30(initialPicked30);
          setPickedTeamsAdvance(initialPickedAdvance);
          setPickedTeams03(initialPicked03);
          
          initialAvailable = initialAvailable.filter(team => !pickedIdsInSlots.has(team.id));
          setAvailableTeams(initialAvailable.sort((a,b) => a.seed - b.seed));
          setError(null);
        })
        .catch(err => {
          console.error("Error fetching stage fantasy info:", err);
          setError("No se pudo cargar la informacin de los picks para esta fase. " + (err.response?.data?.error || err.message));
          setStageInfo(null);
        })
        .finally(() => setIsLoading(false));
    }
  }, [stageId]);

  const handleRemoveTeamFromSlot = (slotCategory: string, slotIndex: number) => {
    if (!stageInfo || stageInfo.fantasy_status !== 'OPEN') return;

    let teamIdToRemove: number | null = null;
    const newPickedTeams30 = [...pickedTeams30];
    const newPickedTeamsAdvance = [...pickedTeamsAdvance];
    const newPickedTeams03 = [...pickedTeams03];
    let teamData: StageTeamInfoForFantasy | null = null;

    if (slotCategory === 'zone-30' && slotIndex < newPickedTeams30.length) {
      teamIdToRemove = newPickedTeams30[slotIndex];
      if (teamIdToRemove !== null) newPickedTeams30[slotIndex] = null;
    } else if (slotCategory === 'zone-advance' && slotIndex < newPickedTeamsAdvance.length) {
      teamIdToRemove = newPickedTeamsAdvance[slotIndex];
      if (teamIdToRemove !== null) newPickedTeamsAdvance[slotIndex] = null;
    } else if (slotCategory === 'zone-03' && slotIndex < newPickedTeams03.length) {
      teamIdToRemove = newPickedTeams03[slotIndex];
      if (teamIdToRemove !== null) newPickedTeams03[slotIndex] = null;
    }

    if (teamIdToRemove !== null) {
      teamData = stageInfo.teams.find(t => t.id === teamIdToRemove) || null;
      if (teamData) {
        const currentAvailableTeams = [...availableTeams];
        if (!currentAvailableTeams.find(t => t.id === teamData!.id)) {
            currentAvailableTeams.push(teamData);
            currentAvailableTeams.sort((a,b) => a.seed - b.seed);
            setAvailableTeams(currentAvailableTeams);
        }
      }
      setPickedTeams30(newPickedTeams30);
      setPickedTeamsAdvance(newPickedTeamsAdvance);
      setPickedTeams03(newPickedTeams03);
    }
  };

  function handleDragStart(event: DragStartEvent) {
    if (event.active.data.current?.type === 'team') {
        setActiveDragItem(event.active.data.current?.team as StageTeamInfoForFantasy);
    }
  }

  function handleDragEnd(event: DragEndEvent) {
    setActiveDragItem(null);
    const { active, over } = event;

    if (!over || !stageInfo || stageInfo.fantasy_status !== 'OPEN') return;

    const draggedTeamData = active.data.current?.team as StageTeamInfoForFantasy;
    if (!draggedTeamData) return;

    const sourceDndId = active.id.toString();
    const targetDndId = over.id.toString();

    const newP30 = [...pickedTeams30];
    const newPAdvance = [...pickedTeamsAdvance];
    const newP03 = [...pickedTeams03];
    let newAvail = [...availableTeams];

    let sourceType: 'slot' | 'available' | null = null;
    let sourceCategory: string | null = null;
    let sourceSlotIndex: number | null = null;
    if (sourceDndId.startsWith('picked-slot-')) {
      sourceType = 'slot';
      const parts = sourceDndId.split('-');
      sourceCategory = `zone-${parts[2]}`;
      sourceSlotIndex = parseInt(parts[3]);
    } else if (sourceDndId.startsWith('team-')) {
      sourceType = 'available';
    }

    let targetType: 'slot' | 'available' | null = null;
    let targetCategory: string | null = null;
    let targetSlotIndex: number | null = null;
    if (targetDndId.startsWith('slot-')) {
      targetType = 'slot';
      const parts = targetDndId.split('-');
      targetCategory = `zone-${parts[1]}`;
      targetSlotIndex = parseInt(parts[2]);
    } else if (targetDndId === 'zone-available') {
      targetType = 'available';
    }

    if (!targetType || !sourceType) return;

    if (sourceType === 'available' && targetType === 'slot') {
      const teamIdInTargetSlot = getTeamIdAtSlot(targetCategory!, targetSlotIndex!, newP30, newPAdvance, newP03);
      placeTeamInSlot(draggedTeamData.id, targetCategory!, targetSlotIndex!, newP30, newPAdvance, newP03);
      newAvail = newAvail.filter(t => t.id !== draggedTeamData.id);
      if (teamIdInTargetSlot && teamIdInTargetSlot !== draggedTeamData.id) {
        const displacedTeam = stageInfo.teams.find(t => t.id === teamIdInTargetSlot);
        if (displacedTeam && !newAvail.find(t => t.id === displacedTeam.id)) {
          newAvail.push(displacedTeam);
        }
      }
    } else if (sourceType === 'slot' && targetType === 'available') {
      placeTeamInSlot(null, sourceCategory!, sourceSlotIndex!, newP30, newPAdvance, newP03);
      if (!newAvail.find(t => t.id === draggedTeamData.id)) {
        newAvail.push(draggedTeamData);
      }
    } else if (sourceType === 'slot' && targetType === 'slot') {
      if (sourceCategory === targetCategory && sourceSlotIndex === targetSlotIndex) {
        // No action if dragged to the same slot
      } else {
        const teamIdInTargetSlot = getTeamIdAtSlot(targetCategory!, targetSlotIndex!, newP30, newPAdvance, newP03);
        if (teamIdInTargetSlot && teamIdInTargetSlot !== draggedTeamData.id) { // Target slot is OCCUPIED by a DIFFERENT team -> SWAP
          placeTeamInSlot(draggedTeamData.id, targetCategory!, targetSlotIndex!, newP30, newPAdvance, newP03); // Dragged team to target slot
          placeTeamInSlot(teamIdInTargetSlot, sourceCategory!, sourceSlotIndex!, newP30, newPAdvance, newP03); // Displaced team to source slot
        } else { // Target slot is EMPTY or contains the same team (though same slot case handled above) -> MOVE
          placeTeamInSlot(null, sourceCategory!, sourceSlotIndex!, newP30, newPAdvance, newP03); // Empty source slot
          placeTeamInSlot(draggedTeamData.id, targetCategory!, targetSlotIndex!, newP30, newPAdvance, newP03); // Dragged team to target slot
        }
      }
    }

    setPickedTeams30(newP30);
    setPickedTeamsAdvance(newPAdvance);
    setPickedTeams03(newP03);
    setAvailableTeams(newAvail.sort((a, b) => a.seed - b.seed));
  }

  const handleSubmitPicks = async () => {
    if (!stageId || !stageInfo || stageInfo.fantasy_status !== 'OPEN') return;
    if (!stageInfo.rules) {
        alert("La configuracin de las reglas para esta fase no est disponible. No se pueden enviar los picks.");
        setError("Faltan las reglas de la fase para poder guardar.");
        return;
    }
    const finalTeams30 = pickedTeams30.filter(id => id !== null) as number[];
    const finalTeamsAdvance = pickedTeamsAdvance.filter(id => id !== null) as number[];
    const finalTeams03 = pickedTeams03.filter(id => id !== null) as number[];

    if (finalTeams30.length !== stageInfo.rules.num_teams_3_0) {
        alert(`Debes seleccionar exactamente ${stageInfo.rules.num_teams_3_0} equipos para la categora '3-0'.`);
        return;
    }
    if (finalTeamsAdvance.length !== stageInfo.rules.num_teams_advance) {
        alert(`Debes seleccionar exactamente ${stageInfo.rules.num_teams_advance} equipos para la categora 'Avanzarn'.`);
        return;
    }
    if (finalTeams03.length !== stageInfo.rules.num_teams_0_3) {
        alert(`Debes seleccionar exactamente ${stageInfo.rules.num_teams_0_3} equipos para la categora '0-3'.`);
        return;
    }
    const payload: FantasyPhasePickPayload = {
      teams_3_0_ids: finalTeams30,
      teams_advance_ids: finalTeamsAdvance,
      teams_0_3_ids: finalTeams03,
    };
    setSubmitMessage('Enviando picks...');
    setError(null);
    setIsLoading(true);
    try {
      await submitFantasyPhasePicks(parseInt(stageId), payload);
      setSubmitMessage('Picks guardados con xito!');
      const updatedData = await getStageFantasyInfo(parseInt(stageId));
      setStageInfo(updatedData);
      const pickedIds = new Set<number>();
      if (updatedData.user_pick) {
        const s30 = updatedData.user_pick.teams_3_0_details;
        const sAd = updatedData.user_pick.teams_advance_details;
        const s03 = updatedData.user_pick.teams_0_3_details;
        const num30Rule = updatedData.rules?.num_teams_3_0 || 0;
        const newPicks30_res = new Array(num30Rule).fill(null);
        s30.forEach((team, idx) => { if(idx < num30Rule) newPicks30_res[idx] = team.id; pickedIds.add(team.id); });
        setPickedTeams30(newPicks30_res);
        const numAdvanceRule = updatedData.rules?.num_teams_advance || 0;
        const newPicksAdvance_res = new Array(numAdvanceRule).fill(null);
        sAd.forEach((team, idx) => { if(idx < numAdvanceRule) newPicksAdvance_res[idx] = team.id; pickedIds.add(team.id); });
        setPickedTeamsAdvance(newPicksAdvance_res);
        const num03Rule = updatedData.rules?.num_teams_0_3 || 0;
        const newPicks03_res = new Array(num03Rule).fill(null);
        s03.forEach((team, idx) => { if(idx < num03Rule) newPicks03_res[idx] = team.id; pickedIds.add(team.id); });
        setPickedTeams03(newPicks03_res);
      } else {
        setPickedTeams30(new Array(updatedData.rules?.num_teams_3_0 || 0).fill(null));
        setPickedTeamsAdvance(new Array(updatedData.rules?.num_teams_advance || 0).fill(null));
        setPickedTeams03(new Array(updatedData.rules?.num_teams_0_3 || 0).fill(null));
      }
      setAvailableTeams(updatedData.teams.filter(team => !pickedIds.has(team.id)).sort((a,b) => a.seed - b.seed));
      setTimeout(() => setSubmitMessage(null), 3000);
    } catch (err: any) {
      console.error("Error submitting picks:", err);
      setError("Error al guardar los picks: " + (err.response?.data?.error || err.response?.data?.detail || err.message));
      setSubmitMessage(null);
    } finally {
      setIsLoading(false);
    }
  };

  const commonPageClasses = "min-h-[calc(100vh-4rem)] relative isolate bg-black text-white";
  const commonContainerClasses = "relative z-10 container mx-auto p-4 sm:p-8 pt-8 text-white";

  if (isLoading && !stageInfo) return (
    <div className={`${commonPageClasses} flex items-center justify-center`}>
        <GridPattern />
        <p className="relative z-10">Cargando informacin de la fase...</p>
    </div>
  );
  if (error && !stageInfo) return (
    <div className={`${commonPageClasses} flex items-center justify-center`}>
        <GridPattern />
        <p className="relative z-10 text-red-500">Error: {error}</p>
    </div>
  );
  if (!stageInfo) return (
    <div className={`${commonPageClasses} flex items-center justify-center`}>
        <GridPattern />
        <p className="relative z-10">No se encontr informacin para esta fase.</p>
    </div>
  );
  
  const getTeamDataForSlot = (teamId: number | null): StageTeamInfoForFantasy | null => {
    if (teamId === null || !stageInfo) return null;
    return stageInfo.teams.find(t => t.id === teamId) || null;
  };

  return (
    <DndContext sensors={sensors} collisionDetection={closestCenter} onDragStart={handleDragStart} onDragEnd={handleDragEnd}>
      <div className={commonPageClasses}>
        <GridPattern />
        <div className={commonContainerClasses.replace("p-4 text-white", "p-4")}>
         <h1 className="text-3xl font-bold mb-2">Picks de Fantasy - {stageInfo.name}</h1>
         <p className="mb-1 text-neutral-400">Estado: <span className={`font-semibold ${stageInfo.fantasy_status === 'OPEN' ? 'text-green-400' : 'text-red-400'}`}>{stageInfo.fantasy_status}</span></p>
         {stageInfo.fantasy_status !== 'OPEN' && <p className="mb-4 text-sm text-yellow-400">Las elecciones para esta fase estn cerradas.</p>}

         <div className="flex flex-col gap-8 mt-6">
           
           {stageInfo.rules ? (
             <div className="space-y-8"> 
               <div className="flex flex-row justify-between items-start gap-4">
                 <div className="flex-1 min-w-0">
                   <h3 className="text-xl font-semibold mb-3 text-neutral-200 text-center">Equipos 3-0 ({pickedTeams30.filter(t=>t!==null).length}/{stageInfo.rules.num_teams_3_0})</h3>
                   <div className="flex flex-row flex-wrap justify-center gap-3">
                     {pickedTeams30.map((teamId, index) => {
                       const teamData = getTeamDataForSlot(teamId);
                       return (
                         <DroppableSlot 
                             key={`slot-30-${index}`} 
                             id={`slot-30-${index}`} 
                             team={teamData}
                             fantasyStatus={stageInfo.fantasy_status}
                             categoryTitle="zone-30"
                             onRemoveClick={() => handleRemoveTeamFromSlot('zone-30', index)}
                             isUnderdogBonus={teamData ? underdogBonusTeamIds.includes(teamData.id) : false}
                         />
                       );
                     })}
                   </div>
                 </div>

                 <div className="flex-1 min-w-0">
                   <h3 className="text-xl font-semibold mb-3 text-neutral-200 text-center">Equipos 0-3 ({pickedTeams03.filter(t=>t!==null).length}/{stageInfo.rules.num_teams_0_3})</h3>
                   <div className="flex flex-row flex-wrap justify-center gap-3">
                     {pickedTeams03.map((teamId, index) => {
                       const teamData = getTeamDataForSlot(teamId);
                       return (
                         <DroppableSlot 
                             key={`slot-03-${index}`} 
                             id={`slot-03-${index}`} 
                             team={teamData}
                             fantasyStatus={stageInfo.fantasy_status}
                             categoryTitle="zone-03"
                             onRemoveClick={() => handleRemoveTeamFromSlot('zone-03', index)}
                             isUnderdogBonus={false}
                         />
                       );
                     })}
                   </div>
                 </div>
               </div>

               <div>
                 <h3 className="text-xl font-semibold mb-3 text-neutral-200 text-center">Avanzarn ({pickedTeamsAdvance.filter(t=>t!==null).length}/{stageInfo.rules.num_teams_advance})</h3>
                 <div className="flex flex-row flex-wrap justify-center gap-3">
                   {pickedTeamsAdvance.map((teamId, index) => {
                     const teamData = getTeamDataForSlot(teamId);
                     return (
                       <DroppableSlot 
                           key={`slot-advance-${index}`} 
                           id={`slot-advance-${index}`} 
                           team={teamData}
                           fantasyStatus={stageInfo.fantasy_status}
                           categoryTitle="zone-advance"
                           onRemoveClick={() => handleRemoveTeamFromSlot('zone-advance', index)}
                           isUnderdogBonus={teamData ? underdogBonusTeamIds.includes(teamData.id) : false}
                       />
                     );
                   })}
                 </div>
               </div>
             </div>
           ) : (
             <div className="space-y-6 flex items-center justify-center bg-neutral-800/30 p-4 rounded-lg shadow-inner border-2 border-dashed border-neutral-700 min-h-[150px]">
               <p className="text-yellow-400 text-center">
                 La configuracin de reglas para esta fase (ej: nmero de equipos por categora) no est disponible. <br/>Por favor, contacta a un administrador.
               </p>
             </div>
           )}
           <div 
             id="zone-available" 
             ref={setAvailableZoneNodeRef} 
             className="bg-neutral-900/70 p-4 rounded-lg shadow-xl min-h-[200px] border border-neutral-700"
           >
             <h2 className="text-2xl font-semibold mb-4 text-neutral-100 text-center">Equipos Disponibles</h2>
             {availableTeams.length === 0 && (
                <p className="text-neutral-500 text-center py-4">No hay ms equipos disponibles o todos han sido seleccionados.</p>
             )}
             <div className="grid grid-cols-8 gap-2 justify-center">
               {availableTeams
                 .slice() 
                 .sort((a, b) => a.seed - b.seed) 
                 .map(team => (
                   <DraggableTeamCard 
                     key={team.id} 
                     id={`team-${team.id}`} 
                     team={team} 
                     isUnderdogBonus={underdogBonusTeamIds.includes(team.id)}
                   />
               ))}
             </div>
           </div>
         </div>

         {stageInfo.fantasy_status === 'OPEN' && (
           <div className="mt-8 text-center">
             <button
               onClick={handleSubmitPicks}
               disabled={isLoading}
               className="bg-primary-600 hover:bg-primary-700 text-white font-bold py-3 px-8 rounded-lg shadow-md transition-colors disabled:opacity-50"
             >
               {isLoading ? 'Guardando...' : 'Guardar Picks'}
             </button>
           </div>
         )}
         {submitMessage && <p className="mt-4 text-center text-green-400">{submitMessage}</p>}
         {error && !submitMessage && <p className="mt-4 text-center text-red-500">{error}</p>}
        </div>
      </div>
      <DragOverlay>
        {activeDragItem ? <TeamCard team={activeDragItem} isSelected={true} isDragOverlay={true} isUnderdogBonus={underdogBonusTeamIds.includes(activeDragItem.id)} /> : null}
      </DragOverlay>
    </DndContext>
  );
};

export default FantasyStagePicks; 